import LangSelect from '@/components/langSelect';
import password from "./password";
import initPassword from "./initPassword";
import { login, getInfo } from '@/api/login';
export default {
  name: 'Login',
  components: {
    LangSelect: LangSelect,
    password: password,
    initPassword: initPassword
  },
  data: function data() {
    var _this = this;

    var validatePass = function validatePass(rule, value, callback) {
      if (value.length < 6) {
        callback(new Error(_this.$t('login.login.cannotBeLessThan6Digits')));
      }

      callback();
    };

    return {
      loginForm: {
        organize: '',
        cellPhone: '',
        password: '',
        isCanEditOrganize: true
      },
      loginRules: {
        password: [{
          required: true,
          trigger: 'blur',
          validator: validatePass
        }]
      },
      loading: false,
      pwdType: 'password',
      showDialogPassword: false,
      redirect: undefined,
      initView: false,
      token: '',
      img: ''
    };
  },
  watch: {
    $route: {
      handler: function handler(route) {
        this.redirect = route.query && route.query.redirect;
      },
      immediate: true
    }
  },
  created: function created() {
    var _this2 = this;

    getInfo().then(function (res) {
      _this2.img = res.data.config.DMSLoginImage;
      _this2.$refs.bg.style.backgroundImage = "url(".concat(_this2.img, ")");
      _this2.$refs.loginBox.style.backgroundImage = "url(".concat(res.data.config.dmsDefaultLoginBGImage, ")");
    });
    window.setTimeout(function () {
      _this2.$store.dispatch('GetInfo', _this2.loginForm).then(function (res) {
        _this2.loginForm.organize = res.data.siteInfo.companyCode;
        _this2.loginForm.isCanEditOrganize = res.data.siteInfo.companyCode === '';
        var contanier = document.getElementById('contanier');
        var mask = document.getElementById('mask');
        contanier.removeChild(mask);
      });
    }, 1000);
  },
  mounted: function mounted() {
    this.$refs.mask.style.height = document.getElementById('app').clientHeight + 'px';
  },
  methods: {
    hiddenColse: function hiddenColse() {
      this.showDialogPassword = false;
    },
    showPwd: function showPwd() {
      if (this.pwdType === 'password') {
        this.pwdType = '';
      } else {
        this.pwdType = 'password';
      }
    },
    handleLogin: function handleLogin() {
      var _this3 = this;

      this.$refs.loginForm.validate(function (valid) {
        if (valid) {
          _this3.loading = true;
          login(_this3.loginForm).then(function (res) {
            if (res && res.data.isNeedResetPassword === 0) {
              _this3.initView = true;
            } else {
              // alert(res.data.redirectUrl)
              window.location.replace(res.data.redirectUrl);
            }
          }).catch(function () {
            _this3.loading = false;
          });
        } else {
          return false;
        }
      });
    },
    initPasswordSubmit: function initPasswordSubmit() {
      this.initPasswordHidden();
      this.$store.commit('SET_TOKEN', this.token); // setToken(this.token)

      this.$router.push({
        path: this.redirect || '/'
      });
    },
    initPasswordHidden: function initPasswordHidden() {
      this.loading = false;
      this.initView = false;
    }
  }
};